import React from "react";
import { Box, Button, Typography } from "@mui/material";

import { LinearProgress } from "@mui/material";

const About = () => {
  return (
    <Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-evenly",
          margin: "10rem 10rem 5rem 10rem",
        }}
      >
        <Box>
          <img alignitems="center" width="75%" src="LA.jpg" alt="about" />
        </Box>
        <Box>
          <Typography
            sx={{
              fontSize: "2rem",
              fontWeight: "bold",
              textDecoration: "underline",
            }}
          >
            ABOUT ME
          </Typography>
          <Typography
            sx={{
              fontSize: "1.5rem",
            }}
          >
            Professional Web Developer with 2 Years of Experience
          </Typography>
          <Typography>
            loremloreloerloreloersdgsdfg sdfgsdgsdf sdfgsdfgagkldfgasds asg
            asdfasdgasdvcvafer agsdf asdfasd sddfgsdfa asdasdg asdasd
            asdasdgasdg asdgasdga asd
          </Typography>
          <Typography>
            asdasgdagadg asdgasdgasdg asdgasgdasdg asdgasdg asd asdg d sdg
            asdgasdg asdgsd s sfsdgag gdasdga
          </Typography>

          <Button variant="contained" download sx={{ margin: "1% 1% 1% 0%" }}>
            Download CV
          </Button>
        </Box>
      </Box>

      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-around",
          alignContent: "space-around",
          margin: "0rem 10rem 2rem 10rem",
        }}
      >
        <Box>
          <Typography>Hard Work</Typography>
          <LinearProgress
            size="lg"
            thickness={1}
            value={25}
            variant="determinate"
            sx={{ marginBottom: "1rem" }}
          />
          <Typography>Creativity</Typography>
          <LinearProgress
            size="lg"
            thickness={1}
            value={50}
            variant="determinate"
            sx={{ marginBottom: "1rem" }}
          />
        </Box>
        <Typography>Web Designing</Typography>
        <LinearProgress
          size="lg"
          thickness={1}
          determinate
          value={75}
          variant="determinate"
          sx={{ marginBottom: "1rem" }}
        />
        <Typography>Web Development</Typography>
        <LinearProgress
          size="lg"
          thickness={"10"}
          determinate
          value={100}
          variant="determinate"
          sx={{ marginBottom: "1rem" }}
        />
      </Box>
    </Box>
  );
};

export default About;
